import { DatasourceOptions, isDatasourceOptions } from '@xip-online-data/datasource';
import {
  ActionConfig,
  BaseConnectorConfig,
  isActionConfigType,
  isBaseConnectorConfigType,
} from '@xip-online-data/types';

export interface SqlSinkConfig {
  action: ActionConfig;
  database: DatasourceOptions;
}

export function isYamlConfigType(obj: SqlSinkConfig): obj is SqlSinkConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    isActionConfigType(obj.action) &&
    isDatasourceOptions(obj.database)
  );
}
